*{
  padding:0;
  margin: 0;
  box-sizing: border-box;
}


body{
  color: white;
  background-color: rgb(30, 30, 45);
}


.main-container{
  width: 100%;
  display: flex;
  flex-direction: column;
  gap:20px;
  align-items: center;
}

.user{
  display: flex;
  flex-direction: row;
  gap:20px;
  justify-content: space-between;
  align-items: center;
}

.lds-facebook {
  display: inline-block;
  position: relative;
  width: 40%;
  height: 80px;
  margin: 0 auto;
}
.lds-facebook div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #fff;
  animation: lds-facebook 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.lds-facebook div:nth-child(1) {
  left: 8px;
  animation-delay: -0.24s;
}
.lds-facebook div:nth-child(2) {
  left: 32px;
  animation-delay: -0.12s;
}
.lds-facebook div:nth-child(3) {
  left: 56px;
  animation-delay: 0;
}
@keyframes lds-facebook {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}
